.fb-auth-modal {

    .fb-plugin-upgrade-wrapper {
        background: $white-color;
        padding: 42px;
        border-radius: 12px;
        width: 100%;
        max-width: 616px;
        max-height: 323px;
        position: relative;

        .fb-template-info {
            display: flex;
            flex-direction: column;
            align-items: center;

            .fb-template-icon {
                display: flex;
                width: 64px;
                height: 64px;
                svg {
                    width: 100%;
                    height: 100%;
                }
            }
            .fb-template-title {
                text-align: center;
                font-family: Onest;
                font-size: 24px;
                font-weight: 600;
                line-height: 130%;
                color: #0B0C0E;
                margin: 14px 0 10px;
            }
            .fb-template-description {
                font-family: Manrope;
                font-size: 16px;
                font-weight: 500;
                line-height: 24px;
                color: #525252;
                margin: 0;
                text-align: center;
            }
        }
        .fb-template-actions {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 24px;

            .fb-plugin-upgrade-btn {
                width: 268px;
                background: $primary-color;
                color: $white-color;
                border: none;
                padding: 14px 20px;
                border-radius: 4px;
                font-family: $heading-font-family;
                font-weight: $font-weight-500;
                font-size: $x-large-font-size;
                line-height: $line-height-26;
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: background-color 0.2s ease;

                &:hover:not(:disabled) {
                    background: #2563eb;
                }

                &:disabled {
                    opacity: 0.7;
                    cursor: not-allowed;
                }
            }
        }
    }
}